<!-- 待办工单详情 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row class="dt-title"
             type="flex"
             align="center">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />

      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">待办工单详情</p>
      </van-col>
    </van-row>

    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单编号：</van-col>
          <span class="dt-item-content">{{order.SecunityCode}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">提交人：</van-col>
          <span class="dt-item-content">{{order.Creater}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">提交时间：</van-col>
          <span class="dt-item-content">{{order.CreateTime | filteDate}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">当前处理人：</van-col>
          <span class="dt-item-content">{{order.ChargeUserName}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单类型：</van-col>
          <span class="dt-item-content">{{allType[order.SecunityType]}}</span>
        </van-row>

        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单状态：</van-col>
          <span class="dt-item-content">{{order.SecunityStatus | filteOrderTStatus}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">工单内容：</van-col>
          <span class="dt-item-content">{{order.SecunityContent}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单编号：</van-col>
          <span class="dt-item-content">{{order.OrderNo}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单金额：</van-col>
          <span class="dt-item-content">{{order.OrderMoney}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">订单商品：</van-col>
          <span class="dt-item-content">{{order.OrderGoods}}</span>
        </van-row>
        <van-row class="dt-top"
                 v-show="isStore">
          <van-col :span="8"
                   class="dt-item-title">物流公司：</van-col>
          <span class="dt-item-content">{{order.LogisticsCompany}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col class="dt-item-title">图片：</van-col>
        </van-row>
        <van-row gutter="10"
                 class="dt-pic">
          <!-- <van-col :span="8"
                   v-for="item in newImgList"
                   :key="item">
            <van-image width="89"
                       height="89"
                       :src="item.DownloadPath" />
          </van-col> -->
          <van-uploader v-model="newImgList"
                        :deletable="false" />
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">处理历史：</van-col>
        </van-row>
        <van-row class="dt-steps">
          <van-steps direction="vertical"
                     :active="0">
            <van-step v-for="item in this.historyList"
                      :key="item">
              <h3>{{item.content}}</h3>
              <p>{{item.date | filteDate}}</p>
            </van-step>
          </van-steps>
        </van-row>
      </van-row>
      <van-row></van-row>
      <van-row type="flex"
               justify="space-around">
        <van-col :span="12"
                 class="dt-button">
          <van-button round
                      style="width:100px"
                      type="info"
                      @click="process">处理</van-button>
        </van-col>
        <van-col :span="12"
                 class="dt-button">
          <van-button round
                      style="width:100px"
                      type="info"
                      @click="redeployShowPicker = true">转派</van-button>
        </van-col>
      </van-row>
      <van-row>
        <van-popup v-model="redeployShowPicker"
                   round
                   position="bottom">
          <van-picker show-toolbar
                      :columns="peoples"
                      title="转派给"
                      @cancel="redeployShowPicker = false"
                      @confirm="onConfirm" />
        </van-popup>

      </van-row>
    </van-row>

  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions, // 工单查询
  UpdateItem, // 工单转派
} from "@/api/workOrder";
import {
  // 获取应用全量用户
  getAllUser,
  //获取部门树详情
  getParentTree,
  //获取部门成员列表
  getParentUser
} from "@/api/userApi";
import { queryTypeInfos } from "@/api/dimension";
import { GetFileList } from "@/api/upFile";
import { getHistory } from "@/utils/order";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      // 详情绑定数据
      order: {},
      // 转派请求数据（加上order）
      redeployData: {
        TransferUserID: "",
        TransferUserName: "",
        //TransferDeptMent: "123",
        //TransferDuty: "部长",
        TransferLinkPhone: "",
      },
      // 转派选择器是否显示
      redeployShowPicker: false,
      // 转派选择器绑定的数组
      peoples: [], // '张建国', '古田乐', '王明亮', '赵心如', '卢西安', '张北京', '刘开放'
      peopleNames: [],
      peopleobj: {},
      allType: {},
      newImgList: [], // 新增时的图片
      isStore: false, // 是否为商城工单
      historyList: [], // 处理历史列表
    };
  },
  filters: {
    filteDate (date) {
      if (date) {
        return date.replace("T", " ");
      }
    },
    filteOrderTStatus (data) {
      let all = {
        1: '待处理',
        2: '已办理',
        3: '已指派',
      }
      return all[data]
    },
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    // 获取应用全量用户
    //this.getFullUser()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getTypeInfos()
    //获取部门树列表
    this.getParentList()
    this.$toast.loading({
      message: '正在加载',
      forbidClick: true,
      duration: 1000,
    });
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () {

  }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    //获取部门树列表
    getParentList () {
      getParentTree().then(response => {
        let parentList = response.data.tree[0].children
        for (let index in parentList) {
          let paretname = parentList[index].name
          let paretid = parentList[index].id
          this.peoples.push({ id: paretid, text: paretname })
        }
        //获取部门成员列表
        for (let index in parentList) {
          let paretid = parentList[index].id
          getParentUser({ "id": paretid }).then(response => {
            let uList = []
            for (let index in response.data.list) {
              let uname = response.data.list[index].real_name
              let uid = response.data.list[index].uid
              let child = {
                uid: uid,
                text: uname
              }
              uList.push(child)
              this.peopleobj[uname] = response.data.list[index]
            }
            for (let index in this.peoples) {
              let pid = this.peoples[index].id
              if (pid === paretid) {
                this.peoples[index].children = uList
              }

            }
          })
        }
      })
    },
    // 工单查询
    getworkOrderList () {
      GetPageContentByConditions({ ID: this.$route.query.ID }).then(response => {
        this.order = response.Data.ContentList[0]
        this.getImgList() // 获取图片
        // 获取处理历史
        getHistory(this.order.SecunityBussId, (data) => {
          if (data.length) {
            this.historyList = data
          } else {
            this.historyList.push({
              date: this.order.CreateTime,
              content: `${this.order.Creater}创建了工单`
            })
            this.historyList = [...this.historyList, ...data]
          }
        })
        // 判断是否为商城工单
        if (this.order.SecunityType == 6) {
          this.isStore = true
        } else {
          this.isStore = false
        }
      })
    },
    // 处理图片查询
    getImgList () {
      let info = {}
      if (this.order.SecunityBussId == "0") {
        info = {
          AssocID: this.order.ID, // 业务具体id
          PageIndex: 1,
          PageSize: 20
        }
      } else {
        info = {
          AssocID: this.order.SecunityBussId, // 业务具体id
          PageIndex: 1,
          PageSize: 20
        }
      }
      GetFileList(info).then(response => {
        let imgList = response.Data.FileList
        imgList.forEach(item => {
          if (item.AssocSubModule == "待处理") {
            this.newImgList.push({ url: item.DownloadPath, isImage: true })
          }
        })
      })
    },
    // 获取工单类型
    getTypeInfos () {
      let info = {
        SysCode: "ProjectSecunityService",
        Key: "SecunityType"
      }
      queryTypeInfos(info).then(response => {
        let type = eval(response.Data.UValue)
        type.forEach(item => {
          this.allType[item.ItemValue] = item.Description
        })
        this.getworkOrderList()
      })
    },
    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    // 处理按钮事件
    process () {
      this.$router.push({
        name: 'OrderOrderProcessing',
        query: {
          ID: this.$route.query.ID,
          modelType: this.$route.query.modelType
        }
      })
    },
    // 转派选择器确定事件
    onConfirm (value) {
      if (value[value.length - 1] == JSON.parse(localStorage.getItem("userInfo")).real_name) {
        this.$toast({
          message: '不能转派给自己',
          position: 'top',
        });
        return
      }
      let userInfo = this.peopleobj[value[value.length - 1]]
      this.redeployData.TransferUserID = userInfo.uid
      this.redeployData.TransferUserName = userInfo.real_name
      this.redeployData.TransferLinkPhone = userInfo.mobile
      this.redeployShowPicker = false
      this.redeployConfirm()
    },
    // 转派确定按钮
    redeployConfirm () {
      this.$dialog.confirm({
        title: '标题',
        message: '确认转派吗？',
      }).then(() => {
        this.order.SecunityStatus = 3
        this.order.Modifier = JSON.parse(localStorage.getItem("userInfo")).real_name
        this.order.ModifyTime = new Date()
        UpdateItem({ ...this.order, ...this.redeployData }).then(response => {
          if (this.$route.query.modelType == 1) {
            this.$router.push({
              name: 'MOOrderHead',
            })
          } else if (this.$route.query.modelType == 2) {
            this.$router.push({
              name: 'OMOrderHead',
            })
          } else {
            this.$router.push({
              name: 'Home',
            })
          }
        })
      }).catch(() => {
        // on cancel
      });

    },
    // 转派取消按钮
    redeployCancel () {
      this.redeployShow = false
    },
    // 返回首页按钮
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../../styles/dimens";
.dt-home {
  height: 100%;
}
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: "SourceHanSansCN-Medium";
  color: #ecedf2;
  // margin-top: px2rem(35);
}

.dt-content {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  // height: 100vh;
  // background-image: url("../../../assets/img/工单背景.png");
  background-image: url("../../../assets/img/背景图.png");
  background-size: 100% 100%;
  // background-attachment: fixed;
  // background-position: 0 calc(#{$headerHeight} + #{$statusBarHeight});

  .dt-table {
    float: left; // 为了清除父子元素外边距重叠
    width: 94%;
    height: 75vh;
    margin: 0 3%;
    margin-top: px2rem(29);
    overflow-y: auto;
    .dt-item-title {
      width: px2rem(90);
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #4c4c4c;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-content {
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: 400;
      text-align: left;
      color: #4c4c4c;
      line-height: px2rem(23);
      width: px2rem(200);
      float: right;
      padding-right: px2rem(25);
    }
    // .van-row {
    //   text-align: left;
    //   margin: px2rem(15) px2rem(30);
    //   font-size: px2rem(13);
    // }
    .dt-top {
      margin-top: px2rem(7.5);
    }
    .dt-pic {
      margin-top: px2rem(7.5);
      padding: 0 px2rem(30);
      overflow-y: auto;
      .dt-img {
        //图片
        position: relative;
        width: 33%;
        height: 100%;
        float: left;
        padding-top: 50%; /*padding-bottom 都可以*/
        .van-image {
          //图片
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .dt-steps {
    padding-left: px2rem(30);
    width: px2rem(300);
  }
  //整个遮罩层
  .dt-wrapper {
    position: fixed;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    // 文字显示框
    .dt-wrapper-content {
      width: 100%;
      margin-top: px2rem(300);
      height: 25%;
      background-color: rgb(255, 255, 255);
      //确定通过吗
      .dt-redeploy-picker {
        padding: px2rem(30);
        border: solid px2rem(1) rgb(12, 2, 2);
        border-radius: px2rem(10);
        overflow: hidden;
        .dt-redeploy-select {
          background: #edf1f5;
          border-radius: px2rem(5);
        }
      }
      //按钮框
      .dt-redeploy-button-box {
        width: 100%;
        margin: px2rem(30)  0;
        // 按钮
        .dt-redeploy-button {
          width: 50%;
          float: left;
          margin: px2rem(10px);
          .dt-button {
            width: 50%;
            padding: 0;
          }
        }
      }
      .dt-wrapper-content {
        width: 100%;
        margin-top: px2rem(300);
        height: 25%;
        background-color: rgb(255, 255, 255);
      }
    }
    .dt-wrapper-content1 {
      width: 100%;
      margin-top: px2rem(300);
      height: 6%;
      background-color: rgb(255, 255, 255);
      //确定通过吗
      .dt-redeploy-picker {
        margin: px2rem(30);
        border: black solid px2rem(1);
        border-radius: px2rem(10);
        overflow: hidden;
      }
      //按钮框
      .dt-redeploy-button-box {
        width: 100%;
        margin: px2rem(30)  0;
        // 按钮
        .dt-redeploy-button {
          width: 50%;
          float: left;
          margin: px2rem(10px);
        }
      }
      .dt-wrapper-content {
        width: 100%;
        margin-top: px2rem(300);
        height: 25%;
        background-color: rgb(255, 255, 255);
      }
    }
  }
}
.dt-button {
  padding-top: px2rem(15);
  text-align: center;
}
/deep/.van-uploader__upload {
  display: none;
}
/deep/.van-uploader {
  float: left;
}
</style>