//  获取工单处理历史
import { GetHistory } from "@/api/workOrder";

export function getHistory (bussId, callback) {
  // 处理历史列表
  let historyList = []
  // 处理时的内容
  let CahrgeContent = ""
  if (bussId !== "0") {
    let info = {
      ID: bussId,
    }
    GetHistory(info).then(response => {
      console.log("历史返回", response);
      response.Data.forEach(item => {
        historyList.push({
          date: item.OperationTime,
          content: `${item.OperatorUserName}转派给了${item.ChargeUserName}`
        })
        if (item.SecunityStatus == 2) {
          historyList.push({
            date: item.ModifyTime,
            content: `${item.Modifier}处理了工单`
          })
          CahrgeContent = item.CahrgeContent
        }
      });
      if (typeof callback == "function") {
        callback(historyList.reverse(), CahrgeContent);
      }
    })
  } else {
    if (typeof callback == "function") {
      callback(historyList, CahrgeContent);
    }
  }
}


