/** 维表 */
import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";

//获取类型，状态列表
/** 
 * 
 */
export function queryTypeInfos (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/BaseDataService/UniformConfig/GetDataBySearch',
    method: 'post',
    data: params
  })
}
