import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";

// 文件上传
export function Upload (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/FileService/File/Upload',
    method: 'post',
    data: params
  })
}

// 获取文件列表
export function GetFileList (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/FileService/File/GetFileList',
    method: 'post',
    data: params
  })
}

// 文件删除
export function DeleteFile (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/FileService/File/DeleteFile',
    method: 'post',
    data: params
  })
}
